<script setup>
import { Buffer } from "buffer";
import { decodeCredential, googleAuthCodeLogin, googleTokenLogin } from 'vue3-google-login'
import { toast } from 'vue3-toastify'
import { useRoute, useRouter } from 'vue-router'
import 'vue3-toastify/dist/index.css'
import router from "@/router";
import { ref } from 'vue'
import useClipboard from 'vue-clipboard3'

const { toClipboard } = useClipboard()
const route = useRoute()
const authToken = ref("")

const callback = async (response) => {
	console.log("response", response);
	//const user = decodeCredential(response.credential)
	var informationBase64String = route.query.information
	var redirectUrl = informationBase64String 
		? Buffer.from(informationBase64String, 'base64').toString('ascii')
		: null
	var responseCredentials = decodeCredential(response.credential);
	var redirectInformation = JSON.stringify(responseCredentials)
	var redirectInformationBase64String = Buffer.from(redirectInformation).toString('base64')
	
	authToken.value = redirectInformationBase64String
	try {
		await toClipboard(redirectInformationBase64String)
	    console.log('Copied to clipboard')
	} catch (e) {
        console.error(e)
	}
	//TODO:: Call backedn API

	console.log("callback", "query", route.query, "informationBase64String", informationBase64String, "responseCredentials", responseCredentials)
	
	if (redirectUrl) {
		var redirectFullUrl = redirectUrl + "?information=" + informationBase64String + "&authentication=" + redirectInformationBase64String;

		console.log("callback", "redirectUrl", redirectUrl, "redirectInformation", redirectInformation, "redirectInformationBase64String", redirectInformationBase64String, "redirectFullUrl", redirectFullUrl)

		if (/^(?:[a-z]+:)?\/\//i.test(redirectUrl)) {
			window.location = redirectFullUrl
		}
		else {
			router.push(redirectFullUrl)
		}
	}
	
	return;
}
const notify = () => {
	const switchNumber = Math.floor((new Date().getTime() / 1000) % 4);
	const notifyMessage = "Use Google sign-in..."
	
	switch(switchNumber) {
		case 0:
		{
			toast.info(notifyMessage); break
		}
		case 1:
		{
			toast.error(notifyMessage); break
		}
		case 2:
		{
			toast.warn(notifyMessage); break
		}
		case 3:
		{
			toast.success(notifyMessage); break
		}
	}
}
</script>

<template>
<div class="container no-select" id="container">
	<div class="form-container sign-in-container">
		<div class="form" xaction="#">
			<h1>GDroid Sign In</h1>
			
			<div class="social-container no-select">
				<GoogleLogin :callback="callback" prompt /> <!-- auto-login popup-type="TOKEN" -->				
			</div>
			<span>or use your account</span>
			<input type="email" placeholder="Email" />
			<input type="password" placeholder="Password" />
			<a @click="notify" href="#">Forgot your password?</a>
			<button @click="notify">Sign In</button>    
		</div>
	</div>
	<div class="overlay-container">
		<div class="overlay">
