				<button @click="notify" class="ghost no-select" id="signUp">Sign Up</button>
			</div>
		</div>
	</div>
</div>
<div class="no-select" style="padding-top: 2rem;">
	<h4>GDroid Ecosystem Central Authentication</h4>
	<h6>Copyright Â© 2020-29 Async Consultancy Pty Ltd</h6>
</div>
</template>

